body,
html {
  background: #f0f0f0;
  position: relative;
  overflow-x: hidden;
}

header,
section,
footer {
  padding: 80px 0;
}

h1 {
  font: 400 90px/1 "Comic CAT", sans-serif;
  text-transform: uppercase;
  color: #0082c9;
}

.btn__red {
  padding: 15px 22px;
  border: none;
  border-radius: 55px;
  background: #ff7f56;
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 8px;
  font: 400 18px/1 "SF Pro Display", sans-serif;
  color: #fff;
  transition: all 0.3s;
  width: max-content;
}
.btn__red:hover {
  background: #ff6635;
}
.btn__trans {
  border: 1px solid #bdd9e8;
  padding: 14px 21px;
  border-radius: 55px;
  background: transparent;
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 8px;
  font: 400 18px/1 "SF Pro Display", sans-serif;
  color: #0082c9;
  transition: all 0.3s;
  width: max-content;
}
.btn__trans:hover {
  color: #fff;
  border: 1px solid #0082c9;
  background: #0082c9;
}

.container {
  max-width: 1200px;
  margin: 0 auto;
  padding: 0 20px;
  box-sizing: content-box;
}
.container__title {
  font: 500 15px/1.2 "SF Pro Display", sans-serif;
  text-transform: uppercase;
  color: #0082c9;
}
.container__wrapper {
  display: grid;
  grid-template-columns: 290px 1fr;
  gap: 10px;
  align-items: start;
}

.nav {
  padding: 19px 0;
}
.nav .container {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.nav__btn {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 15px;
}
.nav .btn__red::before {
  content: "";
  display: block;
  width: 18px;
  height: 19px;
  background: url("../img/heart_trans.svg") center no-repeat;
  background-size: contain;
}
.nav .btn__red:hover {
  background-size: contain;
}
.nav .btn__red:hover::before {
  background: url("../img/heart_full.svg") center no-repeat;
}

.header__wrapper {
  margin: 45px 0 0;
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 10px;
}
.header__wrapper img {
  width: 100%;
  border-radius: 30px;
  height: 400px;
  object-fit: cover;
  opacity: 0;
  transition: all 0.5s 1s;
  transform: translateY(50px);
}
.header__slider {
  margin: 45px 0 0;
}
.header__name {
  font: 400 90px/1 "Comic CAT", sans-serif;
  text-transform: uppercase;
  color: #0082c9;
  max-width: calc(100% - 100px);
}
.header__name span {
  display: block;
  opacity: 0;
  transition: all 0.5s 0.5s;
  transform: translateY(50px);
}
.header__name span + span {
  transition-delay: 0.8s;
}
.header__text {
  font: 400 18px/1.4 "SF Pro Display", sans-serif;
  color: #4686a8;
  max-width: 444px;
  opacity: 0;
  transition: all 0.5s 1s;
  transform: translateY(50px);
}
.header .btn__trans {
  margin: 35px 0 0;
  opacity: 0;
  transition: all 0.3s, transform 0.5s 1s, opacity 0.5s 1s;
  transform: translateY(50px);
}
.header__slide {
  opacity: 0 !important;
  transition: all 0.5s 0s !important;
}
.header__slide.swiper-slide-active {
  opacity: 1 !important;
}
.header__slide.swiper-slide-active .btn__trans {
  opacity: 1;
  transform: translateY(0);
}
.header__slide.swiper-slide-active .header__wrapper img {
  opacity: 1;
  transform: translateY(0);
}
.header__slide.swiper-slide-active .header__name span {
  opacity: 1;
  transform: translateY(0);
}
.header__slide.swiper-slide-active .header__text {
  opacity: 1;
  transform: translateY(0);
}
.header .swiper-button-prev,
.header .swiper-button-next {
  opacity: 1;
  width: 57px;
  height: 47px;
  margin: 0;
  right: auto;
  top: auto;
  transform: none;
  left: 0;
  bottom: 0;
}
.header .swiper-button-prev path,
.header .swiper-button-next path {
  transition: all 0.3s;
}
.header .swiper-button-prev::after,
.header .swiper-button-next::after {
  content: none;
}
.header .swiper-button-prev:hover path, .header .swiper-button-prev.swiper-button-disabled path,
.header .swiper-button-next:hover path,
.header .swiper-button-next.swiper-button-disabled path {
  fill: #82aec5;
}
.header .swiper-button-next {
  left: 82px;
}
.header .swiper-pagination {
  width: max-content;
  left: auto;
  top: auto;
  transform: none;
  right: 0;
  margin: 0;
  bottom: 435px;
  font: 500 35px/1.2 "SF Pro Display", sans-serif;
  color: #bdd9e8;
}
.header .swiper-pagination .swiper-pagination-current {
  color: #0082c9;
}

.fees__wrapper {
  border-bottom: 1px solid #bdd9e8;
}
.fees__prev {
  transition: all 0.3s;
  position: relative;
  overflow: hidden;
  border-radius: 20px;
}
.fees__prev img {
  width: 290px;
  height: 290px;
  object-fit: cover;
}
.fees__info {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  gap: 20px;
}
.fees__info p {
  color: #0082c9;
  transition: all 0.3s;
}
.fees__for {
  font: 400 16px/1.3 "SF Pro Display", sans-serif;
  color: #5d98b9 !important;
}
.fees__name {
  font: 500 35px/1.2 "SF Pro Display", sans-serif;
}
.fees__price {
  font: 400 20px/1.3 "SF Pro Display", sans-serif;
  margin: auto 0 0;
}
.fees__item {
  padding: 35px 0;
  border-top: 1px solid #bdd9e8;
  display: grid;
  grid-template-columns: 290px 1fr 40px;
  gap: 30px;
}
.fees__item:hover .fees__info p {
  color: #82aec5 !important;
}
.fees__item:hover .fees__prev {
  border-radius: 70px;
}
.fees .btn__trans {
  margin: 35px 0 0;
}

.stat__wrapper {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  gap: 10px;
}
.stat__item {
  border-radius: 20px;
  background: #fff;
  padding: 30px 25px 25px;
  min-height: 380px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}
.stat__item:nth-child(2) {
  grid-column: 3/4;
}
.stat__val {
  font: 500 70px "SF Pro Display", sans-serif;
  text-transform: uppercase;
  color: #0082c9;
}
.stat__text {
  font: 400 18px/1.4 "SF Pro Display", sans-serif;
  color: #4686a8;
}

.sup {
  color: #fff;
  background: #0082c9;
}
.sup .container__title {
  color: #e8f2ff;
}
.sup__title {
  font: 400 50px/1.1 "Comic CAT", sans-serif;
  text-transform: uppercase;
  color: #fff;
}
.sup__title span {
  transition: all 0.5s;
  opacity: 0;
  transform: translateY(50px);
  transition-delay: 0s;
  display: block;
}
.sup__title span + span {
  transition-delay: 0.3s;
}
.sup__title span + span + span {
  transition-delay: 0.6s;
}
.sup__title.element-show span {
  opacity: 1;
  transform: translateY(0);
}
.sup__text {
  margin: 40px 0 0;
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 30px;
}
.sup__text p {
  font: 400 18px/1.4 "SF Pro Display", sans-serif;
  color: #d1e9f6;
}
.sup .btn__red {
  margin: 30px 0 0;
}

.method__item {
  border-top: 1px solid #bdd9e8;
  padding: 35px 0;
  display: grid;
  grid-template-columns: 290px 1fr 147px;
  gap: 10px;
}
.method__item:last-child {
  padding: 35px 0 0;
}
.method__name {
  font: 500 25px/1.3 "SF Pro Display", sans-serif;
  color: #0082c9;
}
.method__info {
  display: flex;
  flex-direction: column;
}
.method__info p {
  font: 400 18px/1.4 "SF Pro Display", sans-serif;
  color: #4686a8;
  margin: 0 0 10px;
  max-width: 370px;
}
.method__info .btn__trans {
  margin: auto 0 0;
}
.method__prev {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 147px;
  border-radius: 20px;
  background: #fff;
}

.about__name {
  font: 400 50px/1.1 "Comic CAT", sans-serif;
  text-transform: uppercase;
  color: #0082c9;
}
.about__name span {
  transition: all 0.5s;
  opacity: 0;
  transform: translateY(50px);
  display: inline-block;
}
.about__name.element-show span {
  opacity: 1;
  transform: translateY(0);
}
.about__wrapper {
  margin: 40px 0 0;
  display: grid;
  grid-template-columns: 280px 1fr;
  gap: 20px;
}
.about__text {
  display: flex;
  flex-direction: column;
  gap: 20px;
}
.about__text p {
  font: 400 18px/1.4 "SF Pro Display", sans-serif;
  color: #4686a8;
}
.about__text .btn__trans {
  margin: auto 0 0;
}
.about__prev img {
  height: 342px;
  width: 100%;
  object-fit: cover;
  border-radius: 30px;
}

.parthner {
  background: #0082c9;
  position: relative;
  overflow: hidden;
}
.parthner .container__title {
  color: #e8f2ff;
}
.parthner__wrapper {
  grid-template-columns: 1fr 1fr 1fr;
  gap: 10px;
  display: grid;
}
.parthner__item {
  border: 1px solid rgba(255, 255, 255, 0.2);
  border-radius: 20px;
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 20px;
}
.parthner__item img {
  max-width: 200px;
  width: 100%;
  height: 80px;
  object-fit: contain;
}
.parthner__run {
  max-height: 400px;
}

.news__wrapper {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 10px;
}
.news__prev {
  position: relative;
  overflow: hidden;
  border-radius: 10px;
  transition: all 0.3s;
}
.news__prev img {
  width: 100%;
  height: 200px;
  object-fit: cover;
}
.news__row {
  display: flex;
  justify-content: flex-start;
  align-items: flex-start;
  flex-wrap: wrap;
  gap: 15px;
  margin: 0 0 auto;
}
.news__row p {
  font: 400 14px/1.4 "SF Pro Display", sans-serif;
  color: #77aecc;
}
.news__name {
  font: 500 25px/1.3 "SF Pro Display", sans-serif;
  color: #0082c9;
  max-width: calc(100% - 50px);
  transition: all 0.3s;
}
.news__link {
  position: absolute;
  bottom: 25px;
  right: 25px;
}
.news .btn__trans {
  margin: 35px 0 0;
}
.news__item {
  min-height: 440px;
  display: flex;
  flex-direction: column;
  gap: 18px;
  padding: 25px;
  border-radius: 20px;
  background: #fff;
  position: relative;
}
.news__item:hover .news__prev {
  border-radius: 70px;
}
.news__item:hover .news__name {
  color: #82aec5;
}

.footer {
  background: #0082c9;
}
.footer .container__title {
  color: #e8f2ff;
}
.footer__name {
  font: 400 14px/1.4 "SF Pro Display", sans-serif;
  color: #91cbea;
}